}

@media (max-width: 394px) {
  .layout.s-header {
    flex-direction: column;
    align-items: center;
    align-self: start;
    height: 136px;
    transition-delay: 0.5s;
    transition: 0.5s 0.5s;
    margin-bottom: 20px;
  }

  .s-hero {
    margin-top: 20px;
  }

  .s-contact {
    margin-top: 20px;
  }
}

.font-500 {
  font-weight: 500 !important;
}

